.admin-instructions {
  text-align: left;
  font-size: 14px;
}

.admin-instructions li {
  text-align: left;
  font-size: 14px;
  margin-left: 20px;
}


.admin-button-container {
    display: flex;
}

.scenario-wrapper {
  display: flex;
  flex-direction: column; /* Arrange children vertically */
  align-items: center; /* Center children horizontally */
  justify-content: center; /* Center children vertically */
}

.admin-scenario-edit-box {
    margin-top: 20px;
    resize: none;
    width: 100%;
    border: 1px solid #ccc;
}

/* Container for the buttons */
.admin-menu-options {
  display: flex;
  justify-content: center; /* Center buttons */
  align-items: center; /* Align items vertically */
  width: 100%; /* Full width */
  margin-bottom: 10px;
}

/* Individual button styling */
.admin-menu-button {
  background-color: transparent; /* Transparent background */
  border: 2px solid transparent; /* Set the initial border to transparent */
  color: white; /* Text color */
  padding: 10px 20px; /* Padding */
  text-align: center; /* Text alignment */
  text-decoration: none; /* Text decoration */
  font-size: 16px; /* Font size */
  cursor: pointer; /* Cursor style */
  width: 200px; /* Button width */
  border-radius: 5px; /* Border radius */
  margin: 0 5px; /* Margin */
}
  
/* Button hover state */
.admin-menu-button:hover {
  border-color: white; /* Change border color on hover */
  background-color: rgba(255, 255, 255, 0.3); /* Transparent white background on hover */
}

.admin-menu-container {
  display: flex;
  flex-direction: column;
  background-image: linear-gradient(65deg, rgba(41, 52, 196, 0.8), rgba(78, 120, 217, 0.8) 40%, rgba(41, 137, 196, 0.8));
}

@media screen and (min-width: 600px) {
  .admin-menu-container {
    /* padding: 5rem; */
    padding-top: 1rem;
    padding-bottom: 1rem;
    padding-right: 5rem;
    padding-left: 5rem;
    border: 1px solid white;
    border-radius: 10px;
    margin: 0.5rem;
    margin-top: 8%;
  }
}

  /* Individual button styling */
