table{
    width: 100%;
    margin-left: auto;
    margin-right: auto;
    table-layout: fixed;
  }
  
  tbody{
    width: 100%;
    background-color: transparent;
  }
  
  .sort-button{
    background-color: transparent;
    border: none;
    padding: 5px 10px;
    line-height: 1;
    font-size: 15px;
    color: white;
    cursor: pointer;
  }

  .scenario-button{
    background-color: transparent;
    border: none;
    padding: 5px 10px;
    line-height: 1;
    height: 30px;
    width: 250px;
    font-size: 15px;
    color: white;
    cursor: pointer;
  }
  
  .sort-reverse{
    transform: rotate(180deg);
  }